//
// dropzone.scss
//

.dropzone {
  border: none !important;
  background-color: $input-bg;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='20' ry='20' stroke='%2300EBA8FF' stroke-width='4' stroke-dasharray='10%2c 10' stroke-dashoffset='20' stroke-linecap='square'/%3e%3c/svg%3e");
  border-radius: 20px;
  cursor: pointer;
  min-height: 150px;
  padding: 24px;
  box-shadow: $components-shadow-sm;
  max-width: 600px;
  margin: 0 auto;
  width: 100%;
  transition: 0.25s ease-in-out;

  &__error-message {
    display: inline-block;
    color: #ED1C24;
    font-weight: 600;
    font-size: 12px;

    .dropzone_error & {
      &:before {
        content: '';
        width: 9.6px;
        height: 12px;
        display: inline-block;
        background-image: url("../images/dz-validation-icon.svg");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        vertical-align: middle;
        margin: -1.5px 4px 0 0;

      }
    }
  }

  .dz-message {
    text-align: center;
    margin: 13px 0 33px 0;
  }

  &.dz-started {
      .dz-message {
          display: none;
      }
  }

  h3 {
    visibility: visible;
    opacity: 1;
    transition: 0.25s;
  }

  i {
    color: #00eba8;
    transition: 0.25s;
  }

  &:hover {
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='20' ry='20' stroke='blue' stroke-width='4' stroke-dasharray='10%2c 10' stroke-dashoffset='20' stroke-linecap='square'/%3e%3c/svg%3e");

    i {
      color: #0000FF;
      transform: translateY(-17px);
    }

    h3 {
      visibility: hidden;
      opacity: 0;
    }
  }

  &_error {
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='20' ry='20' stroke='%23ED1C24FF' stroke-width='4' stroke-dasharray='10%2c 10' stroke-dashoffset='20' stroke-linecap='square'/%3e%3c/svg%3e") !important;

    i {
      transform: none !important;
      color: #ED1C24 !important;
    }

    h3 {
      visibility: visible !important;
      opacity: 1 !important;
    }

    .dz-message {
      margin: 17px 0 22px 0;
    }

    .dropzone__error-message {
      margin-bottom: 7px;
    }
  }
}