// 
// two-column-menu.scss
//
body[data-layout-mode="two-column"] {
    .left-side-menu {
        width: calc(#{$twocolumn-sidebar-iconview-width} + #{$twocolumn-sidebar-width});
        background-color: transparent;
        box-shadow: none;
    }

    .sidebar-icon-menu {
        position: fixed;
        width: $twocolumn-sidebar-iconview-width;
        z-index: 500;
        top: 0;
        bottom: 0;
        padding-bottom: 20px;
        background-color: $twocolumn-sidebar-iconview-bg;

        .logo {
            display: block;
            width: 70px;
            height: 70px;
            line-height: 70px;
            text-align: center;
        }

        .nav {
            background-color: transparent;
            margin: $topbar-height auto 0;

            @media (max-width: 767.98px) {
                margin: $topbar-height-mobile auto 0;
            }

            .nav-link {
                text-align: center;
                width: 100%;
                height: 66px;
                color: #212B35;
                transition: 0.25s;
                font-size: 12px;
                font-weight: 600;

                svg {
                    fill: #00eba8;
                    transition: 0.25s;
                    margin-bottom: 2px;
                }

                &.active {
                    background-color: #F6F9FC;
                    color: #0000FF;
                }

                &:hover {
                    svg {
                        transform: translateY(-2px);
                    }
                }

                &.active, &:hover {
                    svg {
                        fill: #0000FF;
                    }
                }
            }
        }
    }

    .sidebar-main-menu {
        display: block;
        position: fixed;
        width: $twocolumn-sidebar-width;
        background-color: $twocolumn-sidebar-bg;
        top: $topbar-height;
        bottom: 0;
        left: $twocolumn-sidebar-iconview-width;
        transition: all .1s ease-out;

        @media (max-width: 767.98px) {
            top: $topbar-height-mobile;
        }

        .sidebar-menu-body {
            padding: 20px;
        }

        .menu-title {
            color: $gray-500 !important;
            margin: 0;
            padding: 10px 20px;
            letter-spacing: .05em;
            font-size: .7rem;
            text-transform: uppercase;
            font-weight: 600;
        }

        .nav {
            >.nav-item {
                >.nav-link {
                    color: $menu-item;
                    font-size: $menu-item-size;
                    font-family: $font-family-base;
                }

                .menu-arrow {
                    right: 10px;
                }
            }

            .nav-link {
                position: relative;
                color: $menu-item;
                padding: 0 29px;
                height: 40px;
                line-height: 40px;
                transition: 0.25s;
                font-weight: 500;

                &:hover {
                    color: $menu-item-active;
                }

                &:focus,
                &.active {
                    color: $menu-item-active;
                    background: #ECF1F8;
                }
            }
        }

        #sidebar-menu {
            .menu-arrow {
                top: 7px;
            }
        }

        .nav-second-level{
            padding-left: 15px;
            list-style: none;
        }
    }

    @include media-breakpoint-up(lg) {
        .navbar-custom {
            left: 0 !important;
            padding-left: 0px;

            .logo-box {
                width: $twocolumn-sidebar-iconview-width;

                .logo-sm {
                    display: none;
                }
            }
        }

        /*&[data-sidebar-size="condensed"] {
            .logo-box {
                width: 0 !important;
            }
        }*/
    }

    &[data-sidebar-color="light"]{
        .logo-box {
            background-color: #ffffff;
        }
    }

    @include media-breakpoint-up(md) {
        .content-page {
            margin-left: calc(#{$twocolumn-sidebar-iconview-width} + #{$twocolumn-sidebar-width});
        }

        .footer {
            left: calc(#{$twocolumn-sidebar-iconview-width} + #{$twocolumn-sidebar-width});
        }

        &[data-sidebar-size="condensed"] {
            .sidebar-main-menu {
                display: none;
            }
        }
    }

    .twocolumn-menu-item {
        display: none;
    }

    &[data-sidebar-color="dark"] {
        .sidebar-main-menu {
            background-color: $bg-leftbar-dark;
    
            .nav {
                >.nav-item {
                    >.nav-link {
                        color: $menu-item-color-dark;
                    }
                }
    
                .nav-link {
    
                    &:hover,
                    &:focus,
                    &.active {
                        color: $menu-item-hover-color-dark;
                    }
                }
    
                .menuitem-active {
                    > a.nav-link {
                        color: $menu-item-active;
                        background-color: rgba($menu-item-active, 0.07);
                    }
    
                    a.active {
                        color: $menu-item-active;
                    }
                }
            }
        }
    }

    &[data-sidebar-color="brand"],
    &[data-sidebar-color="gradient"] {
        .sidebar-main-menu {
    
            .nav {
                >.nav-item {
                    >.nav-link {
                        color: rgba($white, 0.7);
                    }
                }
    
                .nav-link {
    
                    &:hover,
                    &:focus,
                    &.active {
                        color: rgba($white, 0.9);
                    }
                }
            }
        }
    }

    &[data-sidebar-color="brand"] {
        .sidebar-main-menu {
            background-color: $bg-leftbar-brand;
        }
    }
    &[data-sidebar-color="gradient"] {
        .sidebar-main-menu {
            background: $bg-leftbar-gradient;
            background-image: linear-gradient(270deg, rgba(64, 149, 216, 0.15), transparent);
        }
    }

    &[data-layout-width="boxed"] {
        .navbar-custom {
            max-width: calc(#{$boxed-layout-width} - #{$twocolumn-sidebar-iconview-width});
        }

        .sidebar-main-menu {
            position: absolute;
            top: 0;
        }
    }

    &[data-layout-width="boxed"]:not([data-sidebar-size="condensed"]) {
        .footer {
            max-width: calc(#{$boxed-layout-width} - #{calc(#{$twocolumn-sidebar-iconview-width} + #{$twocolumn-sidebar-width})});
        }
    }
}