// Global
body {
  &:after {
    content: '';
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: transparent;
    pointer-events: none;
    z-index: 1001;
    transition: 0.4s;
  }

  &.nav-is-toggled {
    &:after {
      background: rgba(#0000FF, 0.2);

      @media (min-width: 768px) {
        display: none;
      }
    }
  }
}
.animation-stopper * {
  animation: none !important;
  transition: none !important;
}

// Btn
.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 0;
  transition: 0.25s;
  color: #000;
  white-space: nowrap;

  &:hover {
    color: #fff;
  }
  &:focus {
    box-shadow: none !important;
  }

  &_icon {
    border: none;
    outline: none;
    background: none;
    width: auto;
    height: auto;
    padding: 0;

    .add-to-fav-icon__bg,
    .add-to-fav-icon__plus {
      transition: 0.25s;
    }

    &:hover, &:focus {
      .add-to-fav-icon__bg {
        fill: #0000FF;
      }

      .add-to-fav-icon__plus {
        stroke: #fff;
        transform: translate(-1980px,-1089.5px);
      }
    }

    &._arrow {
      padding: 6px;
      color: #00EBA8;
      transition: 0.25s;

      &:hover {
        color: #0000FF;
      }
    }
  }
  &_back {
    width: 20px;
    height: 20px;
    position: fixed;
    transition: 0.25s;

    @media (min-width: 768px) {
      display: flex;
      top: 90px;
      right: 10px;
      z-index: 1;
      border-radius: 50%;
      justify-content: center;
      align-items: center;
      color: #000;
      background: #B6FFE8;

      &:hover {
        color: #fff;
        background: #0000FF;
      }
    }

    @media (max-width: 767.98px) {
      display: block;
      top: 20px;
      right: 20px;
      z-index: 1002;
      background: transparent;
      color: #00EBA8;

      &:hover {
        color: #00EBA8;
      }

      svg {
        width: 100%;
        height: 100%;
        display: block;
      }
    }
  }

  &._tag-del {
    background: #fff;
    border-color: #00EBA8;
    color: #000;
    cursor: default !important;
    margin: 16px 10px 0;
    padding-right: 5px;
    white-space: nowrap;

    .btn_icon {
      position: relative;
      height: 8px;
      width: 8px;
      transition: 0.25s;

      svg {
        display: block;
        width: 100%;
      }

      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 20px;
        height: 20px;
        transform: translate(-50%, -50%);
      }
    }

    &:hover, &:focus {
      border-color: #B6FFE8;
      color: #000;

      .btn_icon {
        transform: scale(1.25);
      }
    }
  }
  &._tag {
    background: #fff;
    border-color: #00EBA8;
    color: #000;
    margin: 16px 10px 0;
    white-space: nowrap;

    &:hover, &:focus {
      background: #0000FF;
      border-color: #0000FF;
      color: #fff;
    }
  }

  // Size
  &._sm {
    font-size: 12px;
    font-weight: 500;
    padding: 0 8px;
    height: 25px;
    line-height: 25px;
  }
  &._lg {
    font-size: 16px;
    padding: 0 12px;
    height: 34px;
    line-height: 34px;
    font-weight: 500;
  }
  &._xl {
    font-size: 18px;
    height: 45px;
    padding: 0 20.5px;

    &:hover {
      transform: scale(0.925);
    }
  }

  // Colors
  &._primary {
    &, &:focus {
      background: #00EBA8;
      border-color: #00EBA8;
      color: #000;
    }
  }
  &._secondary {
    &, &:focus {
      background: #B6FFE8;
      border-color: #B6FFE8;
      color: #000;
    }
  }
  &._primary, &._secondary {
    &:hover {
      background: #0000FF;
      border-color: #0000FF;
      color: #fff;
    }
  }

  &._round {
    border-radius: 30px;
  }
  &._check {
    padding: 0;
    width: 25px;

    & + & {
      margin-left: 10px;
    }
  }
  &._sync-list {
    width: 14px;
    height: 14px;
    padding: 0;
    border-radius: 50%;
  }
  &._social-integration {
    display: flex;
    justify-content: center;
    flex-shrink: 0;
    align-items: center;
    width: 75px;
    height: 75px;
    border-radius: 50%;
    background: #B6FFE8;
    overflow: hidden;
    cursor: default !important;

    i {
      color: #00EBA8;
    }

    ._connected & {
      background: #0000ff;

      i {
        color: #fff;
      }
    }
  }
}

// Link
.link {
  color: #212B35;
  font-weight: 700;
  display: inline-block;
  position: relative;
  z-index: 1;
  white-space: nowrap;

  &:hover {
    color: #212B35;
    &:after {
      height: 14px;
    }
  }

  &:after {
    content: '';
    position: absolute;
    display: block;
    left: 0;
    right: 0;
    bottom: 2px;
    width: 100%;
    height: 3px;
    background: #B6FFE8;
    z-index: -1;
    transition: 0.25s;
  }

  h4 & {
    &:hover {
      &:after {
        height: 17px;
      }
    }
  }
}

// Dropdown
.dropdown {
  &._more {
    position: absolute;
    right: 0;
    top:0;
  }
  &._user {
    z-index: 4;
  }

  &-menu {
    &._actions {
      border-radius: 0;
      border: 1px solid #0000FF;
      box-shadow: none;
      min-width: 128px;
      box-sizing: border-box;
      padding: 0;

      &._sm {
        min-width: 113px;
      }

      .dropdown-menu-inner {
        position: relative;
        width: 100%;
        height: 100%;
        padding: 14px 0 10px;
        background: #fff;

        &:before {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          transform: translate(4px, 4px);
          display: block;
          width: 100%;
          height: 100%;
          background: #0000FF;
          z-index: -1;
        }
      }

      .dropdown-item {
        padding: 6px 8px;
        font-weight: 500;
        font-size: 12px;
        text-align: center;
        transition: 0.25s;

        &:hover {
          background: #F6F9FC;
          color: #0000FF;
        }
      }
    }
  }
}
.dropdown-toggle {
  display: inline-block;
  font-weight: 500;
  padding: 10px;
  transition: color 0.25s, background 0.25s, border 0.25s, transform 0.25s;

  &._outline {
    border: 1px solid #0000FF;
    border-radius: 23px;
    color: #000000;
    font-size: 18px;

    &:hover {
      background: #0000FF;
      color: #fff;
      transform: scale(0.9);

      svg {
        color: #fff;
      }
    }

    svg {
      color: #0000FF;
      transition: transform 0.25s, color 0.25s;
    }
  }

  &._solid {
    border: 1px solid #00EBA8;
    background: #00EBA8;
    border-radius: 23px;
    color: #000000;
    font-size: 18px;

    &:hover {
      background: #0000FF;
      border-color: #0000FF;
      color: #fff;

      svg {
        color: #fff;
      }
    }

    svg {
      color: #fff;
      transition: transform 0.25s, color 0.25s;
    }
  }

  &._lg {
    line-height: 44px;
    padding: 0 19px;

    svg {
      margin-left: 4.6px;
    }
  }

  &._md {
    line-height: 34px;
    padding: 0 15.5px;

    svg {
      margin-left: 4.6px;
    }
  }

  &._icon {
    color: #00EBA8;
    position: relative;

    &:hover {
      color: #0000FF;

      svg {
        transform: translateY(-3px);
      }

      .badge {
        transform: translateX(8px);
      }
    }

    .badge {
      background-color: #ed1c24;
      border-radius: 50%;
      transition: 0.25s;
      position: absolute;
      top: 4px;
      right: 5px;
      color: #fff;
    }
  }

  &._user {
    padding: 0 22px 0 0;
    z-index: 1;

    .user-avatar {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 45px;
      height: 45px;
      border-radius: 50%;
      box-sizing: border-box;
      color: #000;
      font-size: 18px;
      margin: 17.5px 0;
      transition: .25s;
      position: relative;
      overflow: hidden;

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 50%;
        border: 1px solid #00f;
      }
    }

    svg {
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
      color: #00eba8;
      fill: transparent;
      transition: color .25s, fill 0.25s;
    }

    &:hover {
      span {
        background: #00f;
        color: #fff;
        transform: scale(.92);
      }

      svg {
        color: #00f;
        fill: #00f;
      }

      svg circle {
        fill: #00f;
      }
    }
  }

  &._more {
    width: 22px;
    height: 40px;
    background: #00eba8;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: .25s;
    border: none;
    color: #000;
    padding: 0;

    &:hover {
      color: #fff;
      background: #0000FF;
    }
  }

  &._activity {
    min-width: 172px;
    background: #F6F9FC;
    display: inline-block;
    line-height: 34px;
    border-radius: 17px;
    padding: 0 18px 0 21.5px;
    color: #212B35;
    font-size: 12px;
    font-weight: 500;
    transition: 0.25s;

    &:hover {
      color: #fff;
      background: #0000FF;
    }
  }

  svg {
    display: inline-block;
    transition: transform 0.25s;
  }
}

// Statistics card
.knobs {
  display: flex;
  flex: 1;
  justify-content: space-around;

  @media (min-width: 768px) {
    flex-direction: column;
    justify-content: space-between;
    margin-top: 0;
  }

  &._triple {
    margin-top: 38px;
    padding-bottom: 14px;

    @media (max-width: 479px) {
      flex-direction: column;

      & > div {
        & + div {
          margin-top: 22px;
        }
      }
    }
  }

  &._double {
    margin-top: 16px;
    align-items: center;

    @media (min-width: 768px) {
      padding: 30px 0;
    }

    &:after {
      content: '';
      height: 50px;

      @media (max-width: 767.98px) {
        display: none;
      }

      @media (max-width: 374px) {
        flex-direction: column;

        & > div {
          & + div {
            margin-top: 22px;
          }
        }
      }
    }
  }
}

// List Add Success
.confirmation {
  position: relative;
  margin: 13px 12px 27px 22px;
  z-index: 1002;

  &:after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform: translate(-10px, 10px);
    z-index: -1;
  }

  &:before {
    content: '';
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(#DAE8F7, 0.7);
    z-index: -2;
  }

  &._positive {
    border: 1px solid #0000ff;

    &:after {
      background: #0000ff;
    }
  }
  &._negative {
    border: 1px solid #ED1C24;

    &:after {
      background: #ED1C24;
    }
  }
}

// Contact Details
.card {
  &._contact {
    padding: 0;
    flex-direction: row;
    margin: 14px 0 18px;

    @media (max-width: 767.98px) {
      min-height: 170px;
    }
  }
  &._chosen {
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='blue' stroke-width='2' stroke-dasharray='7.5%2c 15' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
  }

  &._no-dash {
    background-image: none;
  }

  &._older-lists {
    padding: 30px;
    margin-bottom: 0;
    background-color: #F6F9FC;
  }

  &._stats {
    align-items: flex-start;
    flex: 1;
    padding-top: 27px;
    padding-right: 10px;
  }

  &._connect {
    padding: 0;
    flex-direction: row;
    height: 100px;

    @media (max-width: 600px) {
      flex-direction: column;
      height: auto;
    }
  }
}
.contact {
  &__avatar,
  &__arrows {
    flex: 0 0 100px;
    height: 100px;
    width: 100px;

    @media (max-width: 767.98px) {
      flex: 0 0 85px;
      height: 85px;
      width: 85px;
    }
  }

  &__avatar-image {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  &__arrows {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F6F9FC;

    @media (max-width: 767.98px) {
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }

  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1;
    padding: 23px 40px;


    @media (max-width: 767.98px) {
      flex-direction: column;
      order: -1;
      padding: 26px;
    }

    @media (max-width: 479px) {
      padding: 16px;

      p {
        font-size: 14px !important;
        margin: 0 !important;
      }

      .contact__info-bottom {
        flex-direction: column !important;
      }

      .list-quality-icon, .table-status {
        margin: 10px 0 0 0  !important;
      }

      .dropdown-rounded-btn {
        max-height: 30px;
        line-height: 28px;
        padding: 0 27px 0 10px;
        font-size: 15px;

        i {
          right: 11px;
          width: 10px;
          display: block;

          svg {
            width: 100%;
          }
        }
      }
    }
  }

  &__info-inner {
    display: flex;
    flex: 1;
    flex-direction: column;


    @media (max-width: 767.98px) {
      align-items: center;
    }
  }

  &__info-bottom {
    display: flex;
    align-items: center;


    @media (max-width: 767.98px) {
      justify-content: space-between;
    }
  }

  &__info-card {
    position: relative;

    @media (max-width: 767.98px) {
      height: 200px;

      .contact__info-inner {
        p {
          margin-bottom: 0 !important;
        }

        &:after {
          content: '';
        }
      }
    }

    @media (max-width: 767.98px) {
      height: 170px;
    }
  }

  &__note-date {
    font-size: 14px;
    margin-bottom: 12px;

    span {
      font-weight: 600;
    }
  }

  &__note + &__note {
    margin-top: 37px;
  }

  &__note-text {
    font-size: 12px;
    font-weight: 500;
    color: #454F5B;
    margin-bottom: 11px;
  }

  &__buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 575.8px) {
      flex-direction: column;
    }
  }

  &__finish {
    display: flex;
    align-items: center;
    transition: opacity 0.25s;

    @media (max-width: 575.8px) {
      flex-direction: column;
    }
  }

  &__option {
    flex: 0 0 45.5%;
    display: flex;
    flex-direction: column;

    /*.card {
      flex: 1;
    }*/
  }

  &__option-or {
    font-size: 16px;
    font-weight: 500;
    color: #454F5B;
    margin-top: 60px;
  }

  &__list-number {
    color: #454F5B;
    font-size: 12px;
    font-weight: 500;
  }

  &__older-list-row {
    display: flex;
    justify-content: space-between;
    align-items: center;

    & + & {
      margin-top: 9px;
    }
  }

  &__older-lists {
    color: #f6f9fc;
    max-height: 111px;
    padding-right: 30px;
    overflow: auto;
    transition: 0.25s;

    &:hover {
      color: #B6FFE8;

      &::-webkit-scrollbar-thumb {
        background: #fff;
      }
    }

    &:active {
      color: #f6f9fc;
    }

    &::-webkit-scrollbar {
      width: 20px;
      background: currentColor;
      transition: 0.5s;
    }

    &::-webkit-scrollbar-thumb {
      background: #00EBA8;
      border: 5px solid currentColor;
      border-radius: 20px;
      transition: 0.5s;
    }

    &::-webkit-scrollbar-thumb:active {
      background: #0000FF;
    }
  }
}

.activity-feed {
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #ECF1F8;
    flex-shrink: 0;
    position: relative;
  }

  &__time {
    width: 52px;
    white-space: nowrap;
  }

  &__date {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 15px;
  }

  &__item {
    display: flex;
    align-items: center;
  }

  &__time {
    font-size: 12px;
    margin-right: 15px;
  }

  &__text {
    font-size: 12px;
    font-weight: 500;

    span, .link {
      font-weight: 700;
    }
  }

  &__icon {
    margin-right: 19px;
  }

  &__day + &__day {
    margin-top: 45px;
  }

  &__item + &__item {
    margin-top: 20px;

    .activity-feed__icon {
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, -100%);
        width: 2px;
        height: 20px;
        background: #DAE8F7;
      }
    }
  }

  &__dropdowns {
    position: absolute;
    top: 40px;
    right: 40px;
    display: flex;

    @media (max-width: 767.98px) {
      position: static;
      margin-bottom: 16px;
    }

    @media (max-width: 576px) {
      flex-direction: column;
      align-items: center;
    }
  }
}

// Add Contacts
.upload-list {
  &__match-item {
    white-space: nowrap;

    & + & {
      margin-top: 12px;
    }
  }

  &__match-outer {
    position: relative;

    &:after {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      bottom: 20px;
      width: 10px;
      background: rgba(#000, 0.2);
    }
  }

  &__match-wrap {
    overflow-x: auto;
    color: #fff;
    transition: color 0.25s;

    &:hover {
      color: #B6FFE8;

      &::-webkit-scrollbar-thumb {
        background: #fff;
      }
    }

    &:active {
      color: #fff;
    }

    &::-webkit-scrollbar {
      height: 20px !important;
      background: currentColor;
      transition: 0.5s;
    }

    &::-webkit-scrollbar-thumb {
      background: #00EBA8;
      border: 6px solid currentColor;
      border-radius: 20px;
      transition: 0.5s;
    }

    &::-webkit-scrollbar-thumb:active {
      background: #0000FF;
    }
  }
}

.bootstrap-select {
  min-width: 137px;

  .btn {
    font-size: 10px;
    font-weight: 500;
    color: #000 !important;
    background-color: #fff !important;
    border-color: #00EBA8 !important;
    padding: 0 4px 0 7px;
    line-height: 20px;
    transition: 0.25s;
    &:not(.bs-placeholder) {
      border-color: #000 !important;
      position: relative;

      &:before {
        content: '';
        display: block;
        width: 13px;
        height: 10px;
        margin-right: 5px;
        background-image: url("../images/icon-check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }

    &:hover {
      border-color: #0000FF !important;
    }
  }

  .dropdown-toggle {
    &:after {
      content: '';
      width: 6px;
      height: 4px;
      font: 0;
      background-image: url("../images/dropdown-arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      margin-top: 1px;
    }

    &.bs-placeholder {
      &, &:active, &:focus, &:hover {
        color: #000;
      }
    }
  }

  & > .dropdown-menu {
    min-width: 130px;
    right: 0 !important;
    left: auto !important;
    margin-top: 3px;
    padding: 0;
    border-radius: 0;
    border: 1px solid #00f;
    overflow: visible !important;
    box-shadow: 3px 3px 0 0 rgba(0,0,255,1);

    .upload-list__match-wrap & {
      transform: translate3d(0px, 0px, 0px) !important;
      overflow: auto !important;
      max-height: 165px !important;
    }

    & > .inner {
      position: relative;
      z-index: 1;

      .upload-list__match-wrap & {
        overflow: visible !important;
      }

      & > .dropdown-menu {
        padding: 14px 0 10px;
        animation: none !important;
      }
    }
  }

  .dropdown-item {
    padding: 6px 8px;
    font-weight: 500;
    font-size: 12px;
    text-align: center;
    transition: .25s;

    &:hover {
      background: #f6f9fc;
      color: #00f;
    }
  }

  &._with-search {
    .btn {
      font-size: 12px;
      padding: 0 9px 0 12px;
      height: 28px;
      border-color: #A0B1BA !important;

      &.bs-placeholder {
        color: #758796 !important;
      }

      &:not(.bs-placeholder) {
        border-color: #212B35 !important;

        &:before {
          display: none;
        }
      }

      &:hover {
        border-color: #0000FF !important;
      }
    }

    &.show {
      .btn {
        border-color: #758796 !important;
        color: #212B35 !important;

        /*&:hover {
          border-color: #0000FF !important;
        }*/
      }
    }

    .dropdown-toggle {
      &:after {
        content: '';
        width: 8px;
        height: 4.94px;
      }
    }

    .dropdown-menu {
      left: 0 !important;
      right: 15px !important;
      max-height: 132px;
      overflow: auto;

      &:hover {
        &::-webkit-scrollbar-thumb {
          background: #0000FF;
        }
      }

      &::-webkit-scrollbar {
        width: 20px;
        background: #fff;
      }

      &::-webkit-scrollbar-thumb {
        background: #00EBA8;
        border: 5px solid #fff;
        border-radius: 20px;
      }

      &::-webkit-scrollbar-thumb:active {
