      }
    }

    .dropdown-item {
      text-align: left;
      padding-left: 26px;
      padding-right: 26px;
    }

    .bs-searchbox {
      padding: 17px 5px 10px 25px;
      position: relative;
      z-index: 2;
      background: #fff;

      .form-control {
        border-color: #F6F9FC;
        background: #F6F9FC;
        height: 22px;
        font-size: 10px;
        padding: 0 8px;

        &:hover, &:focus {
          background: #fff;
          border-color: #0000FF;
        }
      }
    }

    & > .dropdown-menu {
      padding: 0 23px 0 3px !important;

      & > .inner {
