  box-sizing: border-box;
  vertical-align: middle;
  list-style: none;
}
.tln-active {
  display: inline-block;
  padding: 7px 10px;
  width: calc(100% - 48px);
  height: 100%;
  font-size: 12px;
  line-height: 1.8;
  word-break: break-all;
  border-color: #758796;
  border-width: 1px 1px 1px 0;
  border-style: solid;
  background-color: #fff;
  resize: none;
  overflow-wrap: normal;
  overflow-x: auto;
  white-space: pre;
}
.tln-wrapper {
  width: 30px;
  padding: 7px 3px;
  height: 100%;
  word-break: break-all;
  overflow: hidden;
  display: inline-block;
  counter-reset: line;
  background: #DAE8F7;
  border: 1px solid #DAE8F7
}
.tln-line {
  width: 100%;
  display: block;
  text-align: right;
  line-height: 1.8;
  font-size: 12px;
  color: #758796;
}
.tln-line::before {
  counter-increment: line;
  content: counter(line);
  font-size: 12px;
  user-select: none;
}
.paste-excel-textarea {
  transition: color 0.25s;

  &::placeholder {
    color: #758796;
  }

  &:hover {
    &::-webkit-scrollbar {
      background: #B6FFE8;
    }

    &::-webkit-scrollbar-thumb {
      border-color: #B6FFE8;
    }

    &::-webkit-scrollbar-thumb {
      background: #fff;
    }
  }

  &:active {
    &::-webkit-scrollbar {
      background: #fff;
    }

    &::-webkit-scrollbar-thumb {
      border-color: #fff;
    }
  }

  &::-webkit-scrollbar {
    width: 20px;
    background: #fff;
    transition: 0.5s;
  }

  &::-webkit-scrollbar-thumb {
    background: #00EBA8;
    border: 5px solid #fff;
    border-radius: 20px;
    transition: 0.5s;
  }

  &::-webkit-scrollbar-thumb:active {
    background: #0000FF;
  }

  &__wrapper {
    position:relative;
    width: 100%;
    max-width: 730px;
    height:200px;
