    margin-top: -3px;
    margin-bottom: 20px;
  }

  .btn {
    height: 32px;
    line-height: 32px;
  }
}

.modal {
  &._confirm,
  &._negative {
    .modal-dialog {
      pointer-events: auto;
      top: 121px;
      margin: 0 37px 0 363px;
      max-width: none;
      z-index: 1002;

      @media (max-width: 767.98px) {
        margin: 0 20px 0 29px;
        top: 86px;
      }

      &:after {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        transform: translate(-10px, 10px);
        z-index: -1;
      }

      &:before {
        content: '';
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(#DAE8F7, 0.7);
        z-index: -2;
      }
    }
  }

  &._confirm {
    .modal-dialog {
      border: 1px solid #0000ff;

      &:after {
        background: #0000ff;
      }
    }
  }
  &._negative {
    .modal-dialog {
      border: 1px solid #ED1C24;

      &:after {
        background: #ED1C24;
      }
    }
  }

  &-content {
    .close {
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(50%, -50%);
      z-index: 1;
    }
  }
}

// Integrations page
.integrations {
  &__wrap {
    max-width: 1091px;
    width: 100%;
    margin: 0 auto;
  }

  &__item {
    display: inline-block;
    margin: 15px;
    width: 240px;
    height: 40px;
    position: relative;

    .integrations__synch-i,
    .integrations__connect-i,
    .integrations__off-i {
      display: none;
    }

    &._connected .integrations__connect-i,
    &._synched .integrations__synch-i,
    &._off .integrations__off-i {
      display: block;
    }


    &._connected, &._synched {
      .integrations__icon-wrap {
        background: #0000FF;
      }

      .integrations__icon {
        color: #fff;
      }

      .integrations__body {
        border-color: #0000FF;
      }
    }

    &._off {
      pointer-events: none;

      .integrations__icon-wrap {
        background: #ED1C24;
      }

      .integrations__icon {
        color: #fff;
      }

      .integrations__body {
        border-color: #ED1C24;
      }

      .integrations__status {
        color: #ED1C24;
      }
    }

    &:hover {
      .integrations__icon-wrap, .integrations__body {
        background: #0000FF;
      }

      .integrations__icon, .integrations__status, .integrations__name {
        color: #fff;
      }

      .integrations__body {
        border-color: #0000FF;
      }
    }
  }

  &__item-inner {
    display: flex;
    width: 100%;
    height: 100%;
  }

  &__icon-wrap {
    height: 100%;
    width: 40px;
    background: #B6FFE8;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.25s;
  }

  &__icon {
    display: block;
    color: #00EBA8;
    transition: color 0.25s;

    svg {
      display: block;
    }
  }

  &__body {
    border-style: solid;
    border-color: #00EBA8;
    border-width: 1px 1px 1px 0;
    background-color: #fff;
    height: 100%;
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    transition: background-color 0.25s, border-color 0.25s;
  }

  &__name {
    font-size: 14px;
    font-weight: 500;
    color: #000000;
    transition: color 0.25s;
  }

  &__status {
    color: #0000FF;
    transition: color 0.25s;
  }
}
.api-integration-list {
