// Connect service
.connect {
  &__icon-wrap {
    height: 100%;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #B6FFE8;

    @media (max-width: 600px) {
      width: 100%;
      height: 75px;
    }
  }

  &__icon {
    display: block;
    color: #00EBA8;

    svg {
      display: block;
      height: 50px;
      max-width: 50px;
    }
  }

  &__content {
    padding: 0 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1;

    @media (max-width: 600px) {
      flex-direction: column;
      padding: 20px;

      .btn {
        font-size: 16px;
        padding: 0 12px;
        height: 34px;
        line-height: 34px;
        font-weight: 500;
        margin-top: 16px;
      }
    }

    .dropdown-menu._actions {
      min-width: 144px;
      margin-top: 5px;

      .dropdown-item {
        padding: 6px 26px;
        text-align: left;
      }
    }
  }

  &__title-wrap {
    flex: 1;
    display: flex;
    align-items: center;

    @media (max-width: 600px) {
      flex: none;
      flex-direction: column;

      h4 {
        text-align: center;
      }

      .table-status {
        margin: 10px 0 0 0 !important;
      }
    }
  }

  &__cards-wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
  }

  &__cards-main {
    flex: 0 0 calc(70% - 15px);
    align-items: flex-start;

    @media (max-width: 1124px) {
      flex: 0 0 calc(60% - 15px);
    }

    @media (max-width: 1024px) {
      flex: 0 0 100%;
    }
  }

  &__cards-aside {
    flex: 0 0 calc(30% - 15px);

    @media (max-width: 1124px) {
      flex: 0 0 calc(40% - 15px);
    }

    @media (max-width: 1024px) {
      flex: 0 0 100%;
    }
  }

  &__card-aside {
    padding-left: 34px !important;
    padding-bottom: 34px !important;
    background: #F6F9FC;
    align-items: flex-start;
    margin-bottom: 40px;

    @media (max-width: 1024px) {
      margin-bottom: 28px;
    }
  }

  &__store-name {
    width: 150px;
  }
}

.toggle-column {
  &__item {
    display: flex;

    & + & {
      margin-top: 17.5px;
    }

    .dragula-handle {
      display: flex;
      justify-content: center;
      align-items: center;
      width: auto;
      height: auto;
      margin: 0 8px 0 0;

      &:before {
        content: "";
        display: block;
        width: 20px;
        height: 20px;
      }

      svg {
        display: block;
      }
    }
  }
}

.filter-option-inner-inner {
  line-height: 28px;
}

.more-conditions {
  position: relative;

  .btn {
    position: absolute;
    top: 50%;
    right: -35px;
    transform: translateY(-50%);
  }
}
.condition-additional-sign {
  position: absolute;
  top: 50%;
  right: -6px;
  transform: translateY(-50%);
  font-size: 12px;
