}

// Desktop navigation rework
@media (min-width: 768px) {
  .navigation-hovered {
    &:after {
      content: '';
      position: fixed;
      top: 0;
      left: 0;
      width: calc(100vw - 100px);
      height: 100%;
      background: rgba(#DAE8F7, 0.7);
      transform: translateX(100px);
      z-index: 1;
      overflow: hidden;
      transition: none;
    }
  }

  body[data-layout-mode=two-column] .content-page {
    margin-left: 100px;
  }

  .nav-item-link.active {
    background: transparent;
  }

  .nav-drill>.nav-items>.nav-item>.nav-item-link:hover svg {
    transform: none;
  }

  .nav-drill>.nav-items>.nav-item.active>.nav-item-link {
    background-color: transparent;
  }

  .nav-drill>.nav-items>.nav-item:hover>.nav-item-link {
    background-color: #f6f9fc;
    color: #00f;

    svg {
      color: #00f;
    }
  }

  .nav-drill>.nav-items>.nav-item {
    display: block;
  }

  .nav-items.nav-expand-content {
    padding-top: 17px;
  }

  .nav-expand-content {
    transition: none;
  }

  .nav-expand.active>.nav-expand-content {
    opacity: 0;
    visibility: hidden;
  }

  .nav-expand:hover>.nav-expand-content {
    opacity: 1;
    visibility: visible;
  }

  .nav-drill {
    width: auto;
  }
}

// Profile dropdown
.navbar-custom .dropdown-menu.profile-dropdown {
  border-radius: 0;
  top: 50% !important;
  right: 44px !important;
  left: auto !important;
  z-index: -1;
  min-width: 173px;
  max-width: 275px;
  border: 1px solid #0000ff;
  box-shadow: 3px 3px 0px 0px rgba(0,0,255,1);
  background: #fff;
  padding: 0;
  animation-name: fade-in;
  transform: none !important;

  @media (max-width: 767.98px) {
    position: fixed;
    margin: 0 auto;
    left: 50vw !important;
    top: 50vh !important;
    right: auto !important;
    transform: translate(-50%, -50%) !important;
  }
}
.profile-dropdown {
  &__inner {
    padding: 27px 0 18px;
    position: relative;
    font-size: 12px;

    @media (max-width: 767.98px) {
      font-size: 18px;
    }

    .close {
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(50%, -50%);

      @media (min-width: 768px) {
        display: none;
      }
    }
  }

  &__email, &__id {
    padding: 0 25px;
    text-align: center;
  }

  &__email {
    color: #212B35;
    font-weight: 700;
    margin-bottom: 3px;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    @media (max-width: 767.98px) {
      margin-bottom: 9px;
    }
  }

  &__id {
    color: #454F5B;
    font-weight: 500;
    margin-bottom: 2px;

    @media (max-width: 767.98px) {
      margin-bottom: 6px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 6px 25px;
    transition: background-color 0.25s;
    cursor: pointer;

    @media (max-width: 767.98px) {
      padding: 10px 25px;
    }

    &:hover {
      background: #F6F9FC;
    }
  }

  &__link-text {
    color: #212B35;
    font-weight: 500;
    margin-left: 8px;
    transition: color 0.25s;
  }

  &__link:hover &__link-text {
    color: #0000ff;
  }

  &__divider {
    height: 1px;
    background: #DAE8F7;
    margin: 0 25px;
  }

  &__btn {
    border: none;
    border-radius: 0;
    outline: none;
    background: #B6FFE8;
    margin: 0 25px;
    display: flex;
    height: 24px;
    justify-content: center;
    align-items: center;
    width: calc(100% - 50px);
    font-weight: 500;
    transition: background-color 0.25s, color 0.25s;

    @media (max-width: 767.98px) {
      height: 32px;
    }

    &:hover {
      background: #0000FF;
      color: #fff;
    }

    &:focus {
      outline: none !important;
    }
  }

  &__progress {
    padding: 0 25px;
    margin-bottom: 5px;

    .progress {
      border-radius: 0;
      background-color: #DAE8F7;
      height: 10px;

      &-bar {
        background-color: #00EBA8;
      }
    }
  }

  &__discount {
    position: absolute;
    bottom: 0;
    left: -20px;
    transform: translateX(-125%);
    padding: 25px;
    background: #fff;
    border: 1px solid #00EBA8;
    box-shadow: 3px 3px 0px 0px #00EBA8;
    text-align: center;
    animation: discount-slide-in 0.5s forwards 0.75s;
    opacity: 0;

    @media (max-width: 767.98px) {
      display: none;
    }
  }

  ._select2-lg {
    & + .select2-container {
      margin-bottom: 9.5px;

      @media (max-width: 767.98px) {
        padding: 8.5px 0px;
      }

      &:hover {
        .select2-selection--multiple,
        .select2-selection--single {
          background-color: #f6f9fc;
        }
      }

      .select2-selection--multiple,
      .select2-selection--single {
        border: none !important;
        padding-left: 49px;
        position: relative;
        line-height: 30px;
        height: 30px;
        transition: background-color 0.25s;
        background: none;
        font-size: 12px;

        @media (max-width: 767.98px) {
          font-size: 18px;
        }

        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: 25px;
          transform: translateY(-50%);
          width: 16px;
          height: 16px;
          background-image: url("../images/planet.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }

        .select2-selection__rendered {
          line-height: 30px;
          height: 30px;

          &:after {
            content: '';
            display: inline-block;
            width: 8px;
            height: 4.94px;
            background-image: url("../images/dropdown-arrow-lg-blue.svg");
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            margin-left: 8px;
            position: relative;
            top: -1px;
          }
        }
      }
    }
  }
}
.select2-container--user-profile {
